import Image from "next/image";

const EyyabMedia = () => {
  return (
    // <div className="relative bg-black text-white min-h-screen flex items-center justify-center">
    <div className="relative overflow-hidden min-h-screen">
      <div className="absolute inset-0  z-20 max-w-7xl mx-auto px-4 sm:px-6 sm:px-8 py-2">
        <Image
          src="/images/circles.svg"
          alt="background"
          layout="fill"
          objectFit="cover"
        />
        <Image
          src="/images/star_1.svg"
          alt="background"
          className="absolute top-10 left-10"
          width={50}
          height={50}
        />
        <Image
          src="/images/star_2.svg"
          alt="background"
          className="absolute bottom-10 right-10"
          width={50}
          height={50}
        />
        <Image
          src="/images/star_3.svg"
          alt="background"
          className="absolute top-10 right-10"
          width={60}
          height={60}
        />
      </div>

      {/* Şeffaf Kart */}

      <div className="relative z-20 max-w-lg mx-auto px-4 py-2 flex flex-col items-center text-center">
        <h1 className="text-3xl mb-4 font-bold">إيبا ميديا</h1>
        <p className="mb-6">
          فريقنا المبدع جاهز لمساعدتك على التميز في عالم التواصل الاجتماعي ويوفر
          لك حلولًا متكاملة تشمل التصميم الجرافيكي، التصوير الاحترافي، الموشن
          والسوق الرقمي، لتشجيع محتواك والإستراتيجيات المبتكرة، وتساعد في إظهار
          صورتك الرقمية وجذب جمهورك المستهدف.
        </p>
        <button className="bg-[#13a0cb] text-white text-lg px-6 py-3 rounded-3xl shadow-lg">
          خدمات إيبا ميديا
        </button>
        <div className="mt-6">
          <Image
            src="/images/home_page_4.svg"
            alt="left image"
            width={300}
            height={300}
            className="object-contain"
          />
        </div>
      </div>
    </div>
  );
};

export default EyyabMedia;
